<template>
  <div class="map">
    <nav class="nav">
      <ul class="navigation">
        <li
          class="nav__item"
          @click="showi(link)"
          v-for="link in store.state.MainData"
          :key="link"
        >
          <button type="button" class="nav__link">
            <img :src="`${link.icon}`" alt="" class="nav__icon" />
          </button>
        </li>
      </ul>
    </nav>
    <div class="map__wrapper">
      <Transition name="fade">
      <div class="points" v-if="currentCompo">
        <div
          :style="{ bottom: point.bottom, left: point.left }"
          v-for="point in currentCompo"
          :key="point"
          class="point"
        >
          <div class="point__info">
            <img class="point__img" :src="point.img" alt="" />
            <h2 class="point__name">
              {{ point.name }}
            </h2>
            <div class="point__description">{{ point.description }}</div>
          </div>
        </div>
      </div>
      </Transition>
      <img src="../assets/map1.png" alt="" class="map__img" />
    </div>
  </div>
</template>

<script>
import { useStore } from "vuex";
import { onMounted, shallowRef } from "vue";
export default {
  name: "HelloWorld",
  props: {
    msg: String,
  },
  setup() {
    const store = useStore();
    let currentCompo = shallowRef(null);
    function getFromStorage(){
      if(localStorage.length > 1){
        let h = localStorage.getItem("currentCompo");
        currentCompo.value = store.state[h];
      }
     }
    function showi(link) {
      window.history.pushState('page', 'title', `${link.location}`);
      currentCompo.value = store.state[link.compo];
      localStorage.setItem("currentCompo", link.compo);
    }
    onMounted(getFromStorage);
    return {
      increment: () => store.commit("increment"),
      store,
      currentCompo,
      showi,
      getFromStorage,
    };
  },
};
</script>

