import { createStore } from 'vuex';
import { DataAmphibia } from './amphibia/amphibia';
import { DataAnimals } from './animals/animals';
import { DataBirds } from './birds/birds';
import { DataFishes } from './fishes/fishes';
import { DataReptiles } from './reptiles/reptiles';
import { DataInsects } from './insects/insects';
import { MainData } from './main-data';
export default createStore({
    state(){
      return {
        DataAmphibia,
        DataAnimals,
        DataBirds,
        DataFishes,
        DataInsects,
        DataReptiles,
        MainData,
        count: 5,
      }
    },
  });